import { Component } from 'react'
import { Route, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { fetchAllStations, fetchGunsPerStation, fetchManagersPerStation, fetchExecutivesPerStation, setStationDailyRates, addStock, fetchStocksLatest, fetchStocksAll, fetchStationDailyRates, setExecSales, fetchExecutivesAttendance } from '../../actions/authActions'

import Guns from './Guns'
import Managers from '../managers/Managers'
import Executives from '../executives/Executives'
import ViewDailyRates from '../managers/ViewDailyRates'
import SetDailyRates from '../managers/SetDailyRates'
import AddStock from '../managers/AddStock'
import ViewStock from '../managers/ViewStock'
import EnterSales from '../managers/EnterSales'
import ViewSales from '../managers/ViewSales'

import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine.css'

const mapStateToProps = state => ({
  stations: state.auth.stations,
  guns: state.auth.gunsPerStation,
  managers: state.auth.managersPerStation,
  executives: state.auth.executivesPerStation,
  attendance: state.auth.executivesAttendance,
  dailyRates: state.auth.dailyRates
})

class DisplayStationDetails extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    let { station } = this.props
    this.props.fetchGunsPerStation({ stationId: station._id })
    this.props.fetchManagersPerStation({ stationId: station._id })
    this.props.fetchExecutivesPerStation({ stationId: station._id })
  }

  componentDidUpdate (prevProps, prevState) {
    let prevStation = prevProps.station
    let { station } = this.props
    if (prevStation._id !== station._id) {
      this.props.fetchGunsPerStation({ stationId: station._id })
      this.props.fetchManagersPerStation({ stationId: station._id })
      this.props.fetchExecutivesPerStation({ stationId: station._id })
    }
  }

  setStationDailyRates (v) {
    let { station } = this.props
    let data = {
      stationId: station._id,
      date: new Date(),
      rates: v.rates,
      validTill: v.validTill
    }

    this.props.setStationDailyRates(data)
  }

  onAddStock (v) {
    let { station } = this.props
    let data = {
      stationId: station._id,
      date: new Date(),
      invoice: v.invoice,
      stocks: v.stocks
    }

    this.props.addStock(data)
    // this.props.fetchStocksLatest({ stationId: station._id })
    // this.props.fetchStocksAll({ stationId: station._id })
  }

  render () {
    let { setDailyRates } = this.state
    let { station, guns, managers, executives, disableManagerDisplay, dailyRates, match } = this.props
    const { path, url } = match
    let name = station && station.name ? station.name.toUpperCase() : 'UNKNOWN'
    if (match.isExact) {
      return (
        <div style={{ width: '100%' }}>
          <Row style={{ width: '100%', paddingLeft: 10 }}>
            <Col>
              <span style={{ fontSize: 25 }}> {name} </span>
              <button style={{ marginLeft: 10 }} onClick={() => this.setState({ setDailyRates: true })}> Set Daily Rate </button>
              <button style={{ marginLeft: 10 }} onClick={() => this.props.history.push(url + '/rates/view')}> View Daily Rates </button>
              <button style={{ marginLeft: 10 }} onClick={() => this.props.history.push(url + '/stocks/add')}> Add Stock </button>
              <button style={{ marginLeft: 10 }} onClick={() => this.props.history.push(url + '/stocks/view')}> View Stock </button>
            </Col>
          </Row>
          <Row style={{ width: '100%', paddingLeft: 10 }}>
            <Col> <Guns station={station} guns={guns} /> </Col>
            { !disableManagerDisplay ? <Col> <Managers station={station} managers={managers} /> </Col> : null }
            <Col> <Executives station={station} executives={executives} /> </Col>
          </Row>
          {setDailyRates ? <SetDailyRates station={station} guns={guns} onSubmit={this.setStationDailyRates.bind(this)} onCancel={() => this.setState({ setDailyRates: false })} /> : null}
        </div>
      )
    } else {
      return <div>
        <Route path={`${path}/stocks/view`}> <ViewStock show station={station} onCancel={() => this.props.history.goBack()} /> </Route>
        <Route path={`${path}/stocks/add`}> <AddStock show station={station} guns={guns} onSubmit={this.onAddStock.bind(this)} onCancel={() => this.props.history.goBack()} /> </Route>
        <Route path={`${path}/rates/view`}>  <ViewDailyRates show station={station} guns={guns} dailyRates={dailyRates} fetchStationDailyRates={this.props.fetchStationDailyRates} onHide={() => this.props.history.goBack()} /> </Route>
        <Route path={`${path}/sales/enter/:executiveId`}>  <EnterSales executives={executives} openingStock={426.33} fetchGunsPerStation={this.props.fetchGunsPerStation} guns={guns} setExecSales={this.props.setExecSales} hide={() => this.props.history.goBack()} /> </Route>
        <Route path={`${path}/sales/view/:executiveId`}>  <ViewSales executives={executives} attendance={this.props.attendance} fetchExecutivesAttendance={this.props.fetchExecutivesAttendance} hide={() => this.props.history.goBack()} /> </Route>
      </div>
    }
  }
}

export default connect(
  mapStateToProps,
  { fetchAllStations, fetchGunsPerStation, fetchManagersPerStation, fetchExecutivesPerStation, setStationDailyRates, addStock, fetchStocksLatest, fetchStocksAll, fetchStationDailyRates, setExecSales, fetchExecutivesAttendance }
)(withRouter(DisplayStationDetails))
