import { Component } from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Modal from 'react-bootstrap/Modal'
import DropdownButton from 'react-bootstrap/DropdownButton'
import Dropdown from 'react-bootstrap/Dropdown'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { faCheckSquare, faTrash } from '@fortawesome/free-solid-svg-icons'

import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { registerNewOwner, fetchGunTypes } from '../../actions/authActions'

library.add(fab, faCheckSquare, faTrash)

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors,
  gunTypes: state.auth.gunTypes
})

class AddNewCompany extends Component {
  constructor (props) {
    super(props)
    this.state = {}
    this.onSubmit = this.onSubmit.bind(this)
  }
  componentWillMount () {
    this.props.fetchGunTypes()
  }
  onSubmit (e) {
    e.preventDefault()
    this.props.registerNewOwner(this.state, this.props.history)
  }
  render () {
    // const { show } = this.props
    return (
      <Modal.Dialog
        show
        size='md'
        backdrop='static'
        onHide={() => this.props.history.push('/dashboard')}
        style={{ height: '100%', maxHeight: '100%', maxWidth: '100%', width: '100%' }}
      >
        <Modal.Header>
          <Modal.Title>Register New Owner</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ height: '100%', minHeight: 600 }}>
          <Tabs id='controlled-tab-example' style={{ backgroundColor: 'blue', marginBottom: 15 }}>
            <Tab eventKey='basic' title='Basic'>
              <BasicInfo
                onNameChange={(e) => this.setState({ username: e.target.value })}
                onPasswordChange={(e) => this.setState({ password: e.target.value })}
                onPhoneChange={(e) => this.setState({ phone: e.target.value })}
              />
            </Tab>
            <Tab eventKey='stations' title='Stations'>
              <AddStation
                gunTypes={this.props.gunTypes}
                setStation={(stations) => this.setState({ stations })}
                setGuns={(guns) => this.setState({ guns })}
                setManagers={(managers) => this.setState({ managers })}
                setExecs={(executives) => this.setState({ executives })}
              />
            </Tab>
          </Tabs>
        </Modal.Body>
        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.history.push('/dashboard')}> Cancel </Button>
          <Button variant='primary' onClick={this.onSubmit}>Register</Button>
        </Modal.Footer>
      </Modal.Dialog>
    )
  }
}
class BasicInfo extends Component {
  render () {
    return (
      <div>
        <Form.Group controlId='formBasicUsername'>
          <Form.Label>Enter Owner Username</Form.Label>
          <Form.Control type='text' placeholder='Username' onChange={this.props.onNameChange} />
          <Form.Text className='text-muted'>
            We will check the availability
          </Form.Text>
        </Form.Group>

        <Form.Group controlId='formBasicPassword'>
          <Form.Label>Password</Form.Label>
          <Form.Control type='password' placeholder='Password' onChange={this.props.onPasswordChange} />
        </Form.Group>

        <Form.Group controlId='formBasicPhone'>
          <Form.Label>Phone Number</Form.Label>
          <Form.Control type='text' placeholder='Phone number' onChange={this.props.onPhoneChange} />
        </Form.Group>
      </div>
    )
  }
}

class AddStation extends Component {
  constructor (props) {
    super(props)
    this.state = {
      stn: [],
      guns: [],
      managers: [],
      executives: [],
      count: 0
    }
  }

  addClick () {
    this.setState(prevState => ({ stn: [...prevState.stn, ''], count: prevState.count + 1 }))
  }

  handleChange (i, e) {
    const { value } = e.target
    let stn = [...this.state.stn]
    stn[i] = value
    this.setState({ stn }, () => this.props.setStation(this.state.stn))
  }

  removeStation (i) {
    let { count } = this.state
    let stn = [...this.state.stn]
    stn.splice(i, 1)
    this.setState({ stn, count: count - 1 }, () => this.props.setStation(this.state.stn))
  }

  setGuns (i, guns) {
    let newGuns = this.state.guns
    newGuns[i] = guns
    this.setState({ guns: newGuns }, () => this.props.setGuns(this.state.guns))
  }

  setManagers (i, managers) {
    let newManagers = this.state.managers
    newManagers[i] = managers
    this.setState({ managers: newManagers }, () => this.props.setManagers(this.state.managers))
  }

  setExecs (i, executives) {
    let newExecs = this.state.executives
    newExecs[i] = executives
    this.setState({ executives: newExecs }, () => this.props.setExecs(this.state.executives))
  }

  createUI () {
    const { stn } = this.state
    return stn.map((el, i) => (
      <div key={i} style={{ alignItems: 'center', border: '1px black solid', marginBottom: 5 }}>
        <div style={{ marginLeft: 20, alignItems: 'center' }}>
          <FontAwesomeIcon icon={faTrash} style={{ cursor: 'pointer', color: 'red' }} onClick={this.removeStation.bind(this, i)} />
          <input style={{ marginLeft: 10, width: 300 }} placeholder='Station Name' name='stnName' value={stn[i]} onChange={this.handleChange.bind(this, i)} />
        </div>
        <Row style={{ marginLeft: 20 }}>
          <Col md={5} style={{ marginLeft: 10 }}> <AddGun stnIndex={i} stnName={stn[i]} gunTypes={this.props.gunTypes} setGuns={this.setGuns.bind(this)} /> </Col>
          <Col md={5} style={{ borderLeft: '1px solid blue', marginLeft: 10 }}>
            <Row style={{ borderBottom: '1px solid blue' }}> <AddManagerLogin stnIndex={i} setManagers={this.setManagers.bind(this)} /> </Row>
            <Row> <AddExecutive stnIndex={i} setExecs={this.setExecs.bind(this)} /> </Row>
          </Col>
        </Row>
      </div>
    ))
  }

  render () {
    return (
      <div>
        {this.createUI()}
        <input type='button' value='Add Station' onClick={this.addClick.bind(this)} />
      </div>
    )
  }
}

class AddGun extends Component {
  constructor (props) {
    super(props)
    this.structure = {
      id: 0,
      name: '',
      type: ''
    }
    this.state = {
      guns: [],
      count: 0
    }
  }

  addGun () {
    const { stnIndex } = this.props
    this.setState(prevState => ({ guns: [...prevState.guns, {}], count: prevState.count + 1 }), () => this.props.setGuns(stnIndex, this.state.guns))
  }

  removeGun (i) {
    const { stnIndex } = this.props
    let { count } = this.state
    let guns = [...this.state.guns]
    guns.splice(i, 1)
    this.setState({ guns, count: count - 1 }, () => this.props.setGuns(stnIndex, guns))
  }

  handleNameChange (i, e) {
    const { stnIndex } = this.props
    const name = e.target.value
    let guns = [...this.state.guns]
    guns[i]['name'] = name
    guns[i]['id'] = this.generateGunName(name, guns[i]['type'], i)
    this.setState({ guns }, () => this.props.setGuns(stnIndex, guns))
  }

  onTypeSelect (i, type) {
    const { stnIndex } = this.props
    let guns = [...this.state.guns]
    guns[i]['type'] = type
    guns[i]['id'] = this.generateGunName(guns[i]['name'], type, i)
    this.setState({ guns }, () => this.props.setGuns(stnIndex, guns))
  }

  generateGunName (name, type, i) {
    const { stnName } = this.props
    return stnName + '_' + type + '_' + i + '_' + (name ? name.replace(/\s/g, '_') : name)
  }

  createUI () {
    const { gunTypes } = this.props
    const { guns } = this.state
    return guns.map((el, i) => (
      <div key={i} style={{ alignItems: 'center', display: 'flex' }}>
        <input placeholder='Gun Id' value={guns[i]['id']} name='gunId' disabled />
        <DropdownButton
          onSelect={this.onTypeSelect.bind(this, i)}
          id='gun-type'
          title={!guns[i] || !guns[i]['type'] ? 'Select type' : (guns[i]['type'])}
          style={{ fontSize: 20, marginRight: 5 }}>
          {
            gunTypes.map((gt) => <Dropdown.Item eventKey={gt.name}>{gt.name}</Dropdown.Item>)
          }
        </DropdownButton>
        <input placeholder='Gun Name' name='gunName' value={guns[i]['name']}onChange={this.handleNameChange.bind(this, i)} />
        <FontAwesomeIcon icon={faTrash} style={{ cursor: 'pointer', color: 'red' }} onClick={this.removeGun.bind(this, i)} />
      </div>
    ))
  }

  render () {
    return (
      <div>
        {this.createUI()}
        <p />
        <input type='button' value='Add Gun' onClick={this.addGun.bind(this)} />
      </div>
    )
  }
}

class AddManagerLogin extends Component {
  constructor (props) {
    super(props)
    this.state = {
      managers: [],
      count: 0
    }
  }

  addManager () {
    const { stnIndex } = this.props
    this.setState(prevState => ({ managers: [...prevState.managers, {}], count: prevState.count + 1 }), () => this.props.setManagers(stnIndex, this.state.managers))
  }

  removeManager (i) {
    const { stnIndex } = this.props
    let { count } = this.state
    let managers = [...this.state.managers]
    managers.splice(i, 1)
    this.setState({ managers, count: count - 1 }, () => this.props.setManagers(stnIndex, managers))
  }

  handleMgrNameChange (i, e) {
    const { stnIndex } = this.props
    const { value } = e.target
    let managers = [...this.state.managers]
    managers[i]['username'] = value
    this.setState({ managers }, () => this.props.setManagers(stnIndex, managers))
  }

  handleMgrPasswordChange (i, e) {
    const { stnIndex } = this.props
    const { value } = e.target
    let managers = [...this.state.managers]
    managers[i]['password'] = value
    this.setState({ managers }, () => this.props.setManagers(stnIndex, managers))
  }

  createUI () {
    const { managers } = this.state
    return managers.map((el, i) => (
      <div key={i} style={{ alignItems: 'center', display: 'flex' }}>
        <input placeholder='Manager Name' value={managers[i]['name']} name='mgrName' onChange={this.handleMgrNameChange.bind(this, i)} />
        <Form.Control type='password' placeholder='Password' onChange={this.handleMgrPasswordChange.bind(this, i)} />
        <FontAwesomeIcon icon={faTrash} style={{ cursor: 'pointer', color: 'red' }} onClick={this.removeManager.bind(this, i)} />
      </div>
    ))
  }

  render () {
    const len = this.state.managers.length
    const dispValue = 'Add ' + (len > 0 ? 'Another ' : '') + 'Manager'
    return (
      <div>
        {this.createUI()}
        <p />
        <input type='button' value={dispValue} onClick={this.addManager.bind(this)} />
        <hr />
      </div>
    )
  }
}

