import { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import { fetchAllStations, fetchGunsPerStation } from '../../actions/authActions'

import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine.css'

const mapStateToProps = state => ({
  stations: state.auth.stations
})

class Guns extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    let { station } = this.props
    // this.props.fetchGunsPerStation({ stationId: station._id })
  }

  componentDidUpdate (prevProps, prevState) {
    let prevStation = prevProps.station
    let { station } = this.props
    if (prevStation._id !== station._id) {
      // this.props.fetchGunsPerStation({ stationId: station._id })
    }
  }

  onSelectGun (gunId) {
    // console.log('gId: ', gunId)
    this.setState({ gunId, showGunStats: true })
  }

  render () {
    let { gunId, showGunStats } = this.state
    let { station, guns } = this.props
    guns = guns || []
    let gunsDisplay = guns.map((g, i) => {
      return <Gun key={i} gun={g} onSelectGun={this.onSelectGun.bind(this)} />
    })

    // console.log('GN: ', gunId, showGunStats)
    return (
      <div style={{ borderRight: '1px solid black' }}>
        <h4> Guns:  </h4>
        {gunsDisplay.map((g) => g)}
        { gunId && showGunStats ? <GunStats gunId={gunId} hideGunStats={() => this.setState({ showGunStats: false })} /> : null }
      </div>
    )
  }
}

class Gun extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  render () {
    // console.log('G: ', this.props, this.state)
    let { gun } = this.props
    return (
      <div style={{ display: 'flex', cursor: 'pointer', color: 'blue', paddingBottom: 10 }} onClick={() => this.props.onSelectGun(gun._id)}>
        <div> {gun.name} </div>
        <div style={{ paddingLeft: 5 }}> ({gun.type}) </div>
      </div>
    )
  }
}

class GunStats extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  render () {
    // console.log('GStats: ', this.props, this.state)
    let { gunId } = this.props
    return (
      <Modal
        show
        size='md'
        backdrop='static'
        onHide={() => this.props.hideGunStats()}
        // style={{ height: '100%', maxHeight: '100%', maxWidth: '100%', width: '100%' }}
      >
        <Modal.Header>
          <Modal.Title>Gun Stats: {gunId}</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ height: 'inherit', minHeight: 'inherit' }}>
        </Modal.Body>
        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.hideGunStats()}> Cancel </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}

export default connect(
  mapStateToProps,
  { fetchAllStations, fetchGunsPerStation }
)(withRouter(Guns))
