import { Component } from 'react'
import Modal from 'react-bootstrap/Modal'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import DateTimePicker from 'react-datetime-picker'

class SetDailyRates extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  setRate (gunType, e) {
    const { value } = e.target
    let newRates = { ...this.state.rates }
    newRates[gunType] = value

    this.setState({ rates: newRates })
  }

  render () {
    const { validTill } = this.state
    const { station, guns } = this.props
    const allTypes = guns.map((g) => g.type)
    const unique = allTypes.filter((t, i, ar) => ar.indexOf(t) === i)

    const display = unique.map((gunType, i) => {
      return <div key={i} style={{ display: 'flex' }}>
        {gunType}
        <Form.Control type='number' placeholder='Todays Rate' onChange={this.setRate.bind(this, gunType)} />
      </div>
    })

    return (
      <Modal
        show
        size='md'
        backdrop='static'
        onHide={() => this.props.onCancel()}
        style={{ height: '100%', width: '100%', background: 'transparent' }}
      >
        <Modal.Header>
          <Modal.Title>Set Daily Rates of {station.name}</Modal.Title>
        </Modal.Header>

        <Modal.Body style={{ height: 'inherit', minHeight: 'inherit' }}>
          Valid Till: <DateTimePicker format='dd-MM-yy h:mm a' disableClock calendarIcon={null} onChange={(validTill) => this.setState({ validTill })} value={validTill} />
          { display.map((d) => d) }
        </Modal.Body>

        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.onCancel()}> Cancel </Button>
          <Button variant='primary' onClick={() => this.props.onSubmit(this.state)}> Submit </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}

export default SetDailyRates
