import { Component } from 'react'
import { AgGridColumn, AgGridReact } from 'ag-grid-react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Row'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
// import Dropdown from 'react-bootstrap/Dropdown'
import { fetchAllOwners, setOwnerStatus, setOwnerPassword } from '../../actions/authActions'

import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine-dark.css'

const mapStateToProps = state => ({
  owners: state.auth.owners
})

class Owners extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    this.props.fetchAllOwners()
  }

  componentDidUpdate () {
  }

  onGridReady (params) {
    params.api.sizeColumnsToFit()
    this.setState({ api: params.api, columnApi: params.columnApi })
  }

  onSelectionChanged () {
    if (!this.state.api) return
    let selectedRows = this.state.api.getSelectedRows()
    let selected = selectedRows[0]
    this.setState({ selected })
  }

  onToggleOwnerActive (ownerId, isActive) {
    this.props.setOwnerStatus({ ownerId, isActive })
  }

  onShowSetPassword () {
    this.setState({ showSetPassword: true })
  }

  onSetPassword (newPassword) {
    let { selected } = this.state
    this.props.setOwnerPassword({ ownerId: selected._id, newPassword })
    this.setState({ selected: null, showSetPassword: false })
    this.state.api.deselectAll()
  }

  render () {
    const { selected, showSetPassword } = this.state
    const { owners } = this.props
    let onToggleOwnerActive = this.onToggleOwnerActive.bind(this)
    return (
      <div style={{ margin: 20 }}>
        <Row style={{ }}>
          <div className='ag-theme-alpine-dark' style={{ height: 400, width: '100%' }}>
            <AgGridReact rowData={owners} onGridReady={this.onGridReady.bind(this)} rowSelection='single' onSelectionChanged={this.onSelectionChanged.bind(this)} context={{ onToggleOwnerActive }}>
              <AgGridColumn field='_id' headerName='Name' sortable filter />
              <AgGridColumn field='regDate' headerName='Reg Date' sortable filter />
            </AgGridReact>
          </div>
        </Row>
        <Row style={{ marginTop: 5, borderTop: '2px solid black' }}>
          { selected ? <Owner selected={selected} onToggleOwnerActive={onToggleOwnerActive} onShowSetPassword={this.onShowSetPassword.bind(this)} /> : null }
          { showSetPassword ? <SetPassword selected={selected} onSubmit={this.onSetPassword.bind(this)} onCancel={() => this.setState({ selected: null, showSetPassword: false })} /> : null }
        </Row>
      </div>
    )
  }
}

// <AgGridColumn field='isActive' headerName='Active?' sortable filter cellRendererFramework={ActiveCellRenderer} />
class Owner extends Component {
  onToggle () {
    const { selected } = this.props
    const { isActive } = selected
    this.props.onToggleOwnerActive(selected._id, !isActive)
  }
  render () {
    const { selected } = this.props
    const { isActive } = selected
    const color = isActive ? 'red' : 'green'
    return (
      <div style={{ margin: '20', display: 'grid' }}>
        <h4 style={{ marginTop: 15 }}>{this.props.selected._id.toUpperCase()}</h4>
        <button style={{ marginTop: 15, paddingLeft: 5, fontSize: 12 }} onClick={() => this.props.onShowSetPassword(selected)}> Reset Password </button>
        <button style={{ marginTop: 15, paddingLeft: 5, fontSize: 12, background: color, color: 'white' }} onClick={this.onToggle.bind(this)}> {isActive ? 'Disable' : 'Enable'} </button>
      </div>
    )
  }
}

class SetPassword extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  render () {
    let { newPassword } = this.state
    let { onCancel, onSubmit } = this.props
    return (
      <Modal
        show
        size='md'
        backdrop='static'
        onHide={() => this.props.onCancel()}
        style={{ height: '100%', width: '100%', background: 'transparent' }}
      >
        <Modal.Header>
          <Modal.Title>Set Password</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ height: 'inherit', minHeight: 'inherit' }}>
          <Form.Group controlId='formBasicPassword'>
            <Form.Label>Password</Form.Label>
            <Form.Control type='password' value={newPassword} onChange={(e) => this.setState({ newPassword: e.target.value })} placeholder='Password' />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant='secondary' onClick={() => onCancel()}> Cancel </Button>
          <Button variant='primary' onClick={() => onSubmit(newPassword)}> Submit </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}

export default connect(
  mapStateToProps,
  { fetchAllOwners, setOwnerStatus, setOwnerPassword }
)(withRouter(Owners))
