import { Component } from 'react'
// import Container from 'react-bootstrap/Container'
// import Row from 'react-bootstrap/Row'
import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'
import NavDropdown from 'react-bootstrap/NavDropdown'

class Header extends Component {
  constructor (props) {
    super(props)
    this.onSelect = this.onSelect.bind(this)
  }
  onSelect (href, e) {
    e.preventDefault()
    if (href === 'Logout') {
      this.props.logoutUser()
    }
  }
  render () {
    const { auth } = this.props
    const { user } = auth || {}
    const { role, name, username, id } = user || {}

    const displayName = name || username || id || 'Unknown'

    return (
      <Navbar collapseOnSelect bg='dark' variant='dark'>
        <img
          alt=''
          src='../../../logo192.png'
          width='30'
          height='30'
          className='d-inline-block align-top'
        />
        <Navbar.Brand href='#home'>Fuel Manager</Navbar.Brand>
        <Navbar.Toggle aria-controls='responsive-navbar-nav' />
        <Navbar.Collapse id='responsive-navbar-nav'>
          <Nav className='mr-auto'>
            {
              role === 'SUPERADMIN'
                ? <div style={{ display: 'flex' }}>
                  <Nav.Link onClick={() => this.props.history.push('/dashboard/admin/view/stations')}>View Stations</Nav.Link>
                  <Nav.Link onClick={() => this.props.history.push('/dashboard/admin/view/owners')}>View Owners</Nav.Link>
                  <Nav.Link onClick={() => this.props.history.push('/dashboard/admin/register/owner')}>Add New Company</Nav.Link>
                  <Nav.Link onClick={() => this.props.history.push('/dashboard/admin/add/gun-types')}>Add Gun Types</Nav.Link>
                </div> : null
            }
            <NavDropdown title={displayName.toUpperCase()} id='collasible-nav-dropdown' onSelect={this.onSelect}>
              <NavDropdown.Item href='Logout'>Logout</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    )
  }
}

export default Header
