import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Row from 'react-bootstrap/Row'
// import Col from 'react-bootstrap/Col'
import { fetchOwnerAllDetails } from '../../actions/authActions'

import DisplayStations from './DisplayStations'
import DisplayStationDetails from './DisplayStationDetails'

const mapStateToProps = state => ({
  auth: state.auth,
  owner: state.auth.owner
})

class OwnerDashboard extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    const { user } = this.props.auth
    this.props.fetchOwnerAllDetails({ username: user.id })
  }

  render () {
    const stations = this.props.owner && this.props.owner.stations ? this.props.owner.stations : []
    let station = this.state.selectedStation && this.state.selectedStation.length > 0 ? this.state.selectedStation[0] : null

    return <div width='100%'>
      <Row style={{ marginLeft: 30, width: '95%', paddingTop: 25 }}>
        <DisplayStations stations={stations} onStationSelected={(selectedStation) => this.setState({ selectedStation })} />
      </Row>
      <Row style={{ marginLeft: 30, width: '95%', background: '#f1eded' }}>
        { station ? <DisplayStationDetails station={station} /> : null }
      </Row>
    </div>
  }
}

OwnerDashboard.propTypes = {
  logoutUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
}
export default connect(
  mapStateToProps,
  { fetchOwnerAllDetails }
)(OwnerDashboard)
