import { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Row'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import DateTimePicker from 'react-datetime-picker'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrash } from '@fortawesome/free-solid-svg-icons'

import { fetchRatesValidity, fetchGunsPerStation, fetchExecutivesPerStation, setExecSales, fetchExecutivesAttendance } from '../../actions/authActions'
import GunsDropdown from '../dashboard/GunsDropdown'

const mapStateToProps = state => ({
  stations: state.auth.stations,
  attendance: state.auth.executivesAttendance,
  ratesValidity: state.auth.ratesValidity,
  gunsPerStation: state.auth.gunsPerStation
})

class EnterSales extends Component {
  constructor (props) {
    super(props)
    this.state = {
      inTime: new Date(),
      outTime: new Date(),
      cashSales: 0,
      cardSales: 0,
      closingStock: 0,
      errors: {},
      wallets: [
        {
          name: 'Other',
          amount: 0
        }
      ]
    }
  }

  componentDidMount () {
    let { executives, match } = this.props
    this.fetchData(executives, match.params.executiveId)
  }

  componentDidUpdate (prevProps, prevState) {
    if (prevProps.executives.length !== this.props.executives.length) {
      let { executives, match } = this.props
      this.fetchData(executives, match.params.executiveId)
    }
  }

  fetchData (executives, executiveId) {
    let exec = executives.find((e) => e._id === executiveId)
    exec = exec || {}

    let { stationId } = exec
    this.props.fetchGunsPerStation({ stationId: stationId })
    this.props.fetchRatesValidity({ stationId: stationId })
  }

  onSetInTime (inTime) {
    this.setState({ inTime })
  }

  onSetOutTime (outTime) {
    const { errors } = this.state
    const { ratesValidity } = this.props
    const validity = new Date(ratesValidity)
    if (outTime > validity) {
      errors.outTime = 'Outtime must be less than rates validity cutoff'
      console.log('Outtime must be less than rates validity cutoff')
    } else {
      errors.outTime = ''
    }
    this.setState({ outTime, errors })
  }

  isDisabled () {
    const validity = new Date(this.props.ratesValidity)
    const { inTime, outTime, selectedGun } = this.state

    const valid = (inTime < outTime) && (outTime.getTime() <= Date.now()) && selectedGun && (outTime <= validity)

    return !valid
  }

  onWalletUpdate (index, field, value) {
    let updated = [...this.state.wallets]
    updated[index][field] = value
    this.setState({ wallets: updated })
  }

  onWalletAdd () {
    let walletsNew = [ ...this.state.wallets ]
    walletsNew.push({ name: '', amount: 0 })
    this.setState({ wallets: walletsNew })
  }

  onDeleteWallet (index) {
    let updated = [...this.state.wallets]
    updated.splice(index, 1)
    this.setState({ wallets: updated })
  }

  render () {
    let { inTime, errors, outTime, cashSales, cardSales, closingStock } = this.state
    let { ratesValidity, match, executives, guns, openingStock } = this.props

    let exec = executives.find((e) => e._id === match.params.executiveId)
    exec = exec || {}

    let { name } = exec
    name = name || 'Unknown'

    const isDisabled = this.isDisabled()

    let walletsSales = 0
    this.state.wallets.forEach((e) => { walletsSales = walletsSales + (isNaN(e.amount) ? parseInt(0) : parseFloat(e.amount)) })

    const totalSales = cashSales + walletsSales + cardSales

    const maxDate = new Date(ratesValidity)

    return (
      <div>
        <Modal.Header> <Modal.Title>{name.toUpperCase()}</Modal.Title> </Modal.Header>
        <Modal.Body style={{ height: 'inherit' }}>
          <Row> Gun:
            <GunsDropdown guns={guns} onSelect={(selectedGun) => this.setState({ selectedGun })} />
          </Row>
          <Row style={{ marginTop: 10 }}> In Time:
            <DateTimePicker format='dd-MM-yy h:mm a' disableClock calendarIcon={null} onChange={this.onSetInTime.bind(this)} value={inTime} />
          </Row>
          <Row style={{ marginTop: 10 }}> Out Time:
            <DateTimePicker format='dd-MM-yy h:mm a' disableClock calendarIcon={null} maxDate={maxDate} onChange={this.onSetOutTime.bind(this)} value={outTime} />
            <Form.Text type='invalid' muted> {errors.outTime} </Form.Text>
          </Row>
          <Row style={{ marginTop: 10 }}> Cash Sales:
            <Form.Control required name='Cash Sales' type='number' value={cashSales} onChange={(e) => this.setState({ cashSales: !isNaN(e.target.value) ? parseFloat(e.target.value) : 0 })} />
          </Row>
          <Row style={{ marginTop: 10 }}> Wallets:
            <Wallets wallets={this.state.wallets} onWalletAdd={this.onWalletAdd.bind(this)} onDeleteWallet={this.onDeleteWallet.bind(this)} onWalletUpdate={this.onWalletUpdate.bind(this)} />
          </Row>
          <Row style={{ marginTop: 10 }}> Card Sales:
            <Form.Control required name='Closing Sales' type='number' value={cardSales} onChange={(e) => this.setState({ cardSales: !isNaN(e.target.value) ? parseFloat(e.target.value) : 0 })} />
          </Row>
          <Row style={{ marginTop: 10 }}> Total Sales (Calculated):
            <Form.Control name='Total Sales' type='number' disabled value={totalSales} />
          </Row>
          <Row style={{ marginTop: 10 }}> <br /> </Row>
          <Row style={{ marginTop: 10 }}> Opening Stock:
            <Form.Control disabled name='Opening Stock' type='number' maxLength='10' value={openingStock} />
          </Row>
          <Row style={{ marginTop: 10 }}> Closing Stock (Meter Reading):
            <Form.Control required name='Closing Stock (Meter Reading)' type='number' value={closingStock} onChange={(e) => this.setState({ closingStock: !isNaN(e.target.value) ? parseFloat(e.target.value) : 0 })} />
          </Row>
        </Modal.Body>
        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.hide()}> Close </Button>
          <Button variant='primary' onClick={() => this.props.setExecSales({ executiveId: match.params.executiveId, inTime, outTime, cashSales, walletsSales, cardSales, totalSales, closingStock })} disabled={isDisabled}> Submit </Button>
        </Modal.Footer>
      </div>
    )
  }
}

class Wallets extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  render () {
    const { wallets } = this.props
    const display = wallets.map((w, i) => <Wallet key={i} wallet={w} index={i} onWalletUpdate={this.props.onWalletUpdate} onDeleteWallet={this.props.onDeleteWallet} />)
    return (
      <div>
        <span style={{ cursor: 'pointer', background: 'green', marginLeft: 10 }} onClick={this.props.onWalletAdd}> Add wallet </span>
        {display.map((d) => d)}
      </div>
    )
  }
}

class Wallet extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  render () {
    const { index, wallet } = this.props
    return (
      <div style={{ display: 'flex' }}>
        <Form.Control required type='text' value={wallet.name} onChange={(e) => this.props.onWalletUpdate(index, 'name', e.target.value)} />
        <Form.Control required type='number' value={wallet.amount} onChange={(e) => this.props.onWalletUpdate(index, 'amount', e.target.value)} />
        <FontAwesomeIcon icon={faTrash} style={{ cursor: 'pointer', color: 'red' }} onClick={() => this.props.onDeleteWallet(index)} />
      </div>
    )
  }
}

export default connect(
  mapStateToProps,
  { fetchRatesValidity, fetchGunsPerStation, fetchExecutivesPerStation, setExecSales, fetchExecutivesAttendance }
)(withRouter(EnterSales))
