import { Component } from 'react'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import { AgGridColumn, AgGridReact } from 'ag-grid-react'
// import Row from 'react-bootstrap/Row'
// import Col from 'react-bootstrap/Col'

class ViewDailyRates extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    this.props.fetchStationDailyRates({ stationId: this.props.station._id })
  }

  componentDidUpdate (prevProps, prevState) {
    if (!prevProps.show && this.props.show) {
      this.props.fetchStationDailyRates({ stationId: this.props.station._id })
    }
  }

  render () {
    const { station, show, dailyRates } = this.props
    const safe = dailyRates && dailyRates.length > 0 ? dailyRates : []

    let types = {}
    let ratesByType = []
    safe.forEach((r) => {
      const { rates } = r
      let row = {}
      row.date = r.date
      for (const type in rates) {
        types[type] = 1
        row[type] = rates[type]
      }
      ratesByType.push(row)
    })

    return (
      <Modal.Dialog
        show={show}
        onHide={() => this.props.onHide()}
        style={{ background: 'transparent', maxWidth: window.innerWidth - 50 }}
      >
        <Modal.Header>
          <Modal.Title>Daily Rates of {station.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ height: 'inherit', width: window.innerWidth - 50, minHeight: 'inherit' }}>
          <div className='ag-theme-alpine-dark' style={{ height: 500, width: '100%' }}>
            <AgGridReact rowData={ratesByType} rowSelection='single'>
              <AgGridColumn field='date' sortable filter />
              {
                Object.keys(types).map((t) => {
                  return <AgGridColumn field={t} sortable filter />
                })
              }
            </AgGridReact>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.onHide()}> Close </Button>
        </Modal.Footer>
      </Modal.Dialog>
    )
  }
}

export default ViewDailyRates
