import React, { Component } from 'react'
import { Route, withRouter } from 'react-router-dom'
import Row from 'react-bootstrap/Row'
// import Col from 'react-bootstrap/Col'
import { connect } from 'react-redux'
import { logoutUser, fetchAllStations } from '../../actions/authActions'

import DisplayStations from './DisplayStations'
import DisplayStationDetails from './DisplayStationDetails'

import RegisterNewOwner from './RegisterNewOwner'
import Owners from './Owners'
import AddEditGunTypes from './AddEditGunTypes'

const mapStateToProps = state => ({
  stations: state.auth.stations
})

class AdminDashboard extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }
  componentDidMount () {
    this.props.fetchAllStations()
  }

  render () {
    const { stations, match, history, location } = this.props
    const { path } = match
    if (match.isExact || location.pathname === `${path}/view/stations`) {
      let station = this.state.selectedStation && this.state.selectedStation.length > 0 ? this.state.selectedStation[0] : null
      return <div width='100%'>
        <Row style={{ marginLeft: 30, width: '95%', paddingTop: 25 }}>
          <DisplayStations stations={stations} onStationSelected={(selectedStation) => this.setState({ selectedStation })} />
        </Row>
        <Row style={{ marginLeft: 30, width: '95%', background: '#f1eded' }}>
          { station ? <DisplayStationDetails station={station} /> : null }
        </Row>
      </div>
    } else {
      return <div>
        <Route exact path={`${path}/register/owner`} component={RegisterNewOwner} />
        <Route exact path={`${path}/view/owners`} component={Owners} />
        <Route exact path={`${path}/add/gun-types`}> <AddEditGunTypes history={history} back={path} /> </Route>
      </div>
    }
  }
}

export default connect(
  mapStateToProps,
  { logoutUser, fetchAllStations }
)(withRouter(AdminDashboard))
