import { Component } from 'react'
import { AgGridColumn, AgGridReact } from 'ag-grid-react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// import { fetchAllStations } from '../../actions/authActions'

import 'ag-grid-community/dist/styles/ag-grid.css'
// import 'ag-grid-community/dist/styles/ag-theme-balham-dark.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine-dark.css'

const mapStateToProps = state => ({
  // stations: state.auth.stations
})

class DisplayStations extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate () {
  }

  onGridReady (params) {
    params.api.sizeColumnsToFit()
    this.setState({ api: params.api, columnApi: params.columnApi })
  }

  onSelectionChanged () {
    if (!this.state.api) return
    var selectedRows = this.state.api.getSelectedRows()
    this.props.onStationSelected(selectedRows)
    // document.querySelector('#selectedRows').innerHTML = selectedRows.length === 1 ? selectedRows[0].athlete : '';
  }

  render () {
    const rowData = this.props.stations || []
    return (
      // <div className='ag-theme-balham-dark' style={{ height: 400, width: '100%' }}>
      <div className='ag-theme-alpine-dark' style={{ height: 400, width: '100%' }}>
        <AgGridReact rowData={rowData} onGridReady={this.onGridReady.bind(this)} rowSelection='single' onSelectionChanged={this.onSelectionChanged.bind(this)}>
          <AgGridColumn field='name' sortable filter />
          <AgGridColumn field='address' sortable filter />
          <AgGridColumn field='owner' sortable filter cellRendererFramework={OwnerCellRenderer} />
        </AgGridReact>
      </div>
    )
  }
}

class OwnerCellRenderer extends Component {
  render () {
    let { data } = this.props
    let { ownerId } = data || {}
    let { _id } = ownerId || 'Unknown'
    return <div> {_id} </div>
  }
}

export default connect(
  mapStateToProps,
  { }
)(withRouter(DisplayStations))
