import { Component } from 'react'
import { connect } from 'react-redux'
import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrash } from '@fortawesome/free-solid-svg-icons'

import { addGunTypes, fetchGunTypes, deleteGunType } from '../../actions/authActions'

const mapStateToProps = state => ({
  auth: state.auth,
  gunTypes: state.auth.gunTypes
})

class AddEditGunTypes extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentWillMount () {
    this.props.fetchGunTypes()
  }
  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  onSubmit () {
    const { gunType } = this.state
    // const test = ['abc', 'def', 'ghi']
    this.props.addGunTypes({ types: [gunType] })
    this.setState({ gunType: null })
    // this.props.history.push('/dashboard')
  }

  render () {
    // let { station } = this.props
    // this.props.deleteGunType({ name: g.name })
  // <FontAwesomeIcon icon={faTrash} style={{ cursor: 'pointer', color: 'red' }} onClick={(e) => this.props.deleteGunType({ name: e.target.name })} />
    const { gunTypes } = this.props
    let display = gunTypes.map((g, i) => {
      return (
        <div key={i}>
          <span> {g.name} </span>
          <span>
            <FontAwesomeIcon icon={faTrash} style={{ cursor: 'pointer', color: 'red' }} onClick={(e) => this.props.deleteGunType({ name: g.name })} />
          </span>
        </div>
      )
    })

    const disabled = !this.state.gunType

    return (
      <Modal
        show
        size='md'
        backdrop='static'
        onHide={() => this.props.history.push(this.props.back)}
        style={{ height: '100%', width: '100%', background: 'transparent' }}
      >
        <Modal.Header>
          <Modal.Title>Add / Edit Gun Types</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ height: 'inherit', minHeight: 'inherit' }}>
          { display.map((d) => d) }
          <Form.Group controlId='gunTypeName' style={{ marginTop: 15 }}>
            <Form.Control type='text' value={this.state.gunType} placeholder='Enter New Gun Type' onChange={(e) => this.setState({ gunType: e.target.value })} onKeyPress={(t) => t.charCode === 13 && this.state.gunType ? this.onSubmit() : null} />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.history.push('/dashboard/admin')}> Cancel </Button>
          <Button variant='primary' onClick={this.onSubmit.bind(this)} disabled={disabled}> Submit </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}

export default connect(
  mapStateToProps,
  { addGunTypes, fetchGunTypes, deleteGunType }
)(AddEditGunTypes)
