import { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import DropdownButton from 'react-bootstrap/DropdownButton'
import Dropdown from 'react-bootstrap/Dropdown'

const mapStateToProps = state => ({
  stations: state.auth.stations,
  attendance: state.auth.executivesAttendance
})

class GunsDropdown extends Component {
  constructor (props) {
    super(props)
    this.state = {
      selected: 'Select Gun'
    }
  }
  onTypeSelect (item) {
    this.setState({ selected: item }, () => this.props.onSelect(item))
  }

  render () {
    const { selected } = this.state
    const { guns } = this.props
    return <div style={{ alignItems: 'center', display: 'flex' }}>
      <DropdownButton
        onSelect={this.onTypeSelect.bind(this)}
        title={selected}
        id='gun-type'
        style={{ fontSize: 20, marginRight: 5 }}>
        {
          guns.map((g, i) => {
            return <Dropdown.Item key={i} eventKey={g['name']}>{g['name']}</Dropdown.Item>
          })
        }
      </DropdownButton>
    </div>
  }
}

export default connect(
  mapStateToProps,
  { }
)(withRouter(GunsDropdown))
