import { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import Col from 'react-bootstrap/Col'

import { fetchGunsPerStation, fetchExecutivesPerStation, setExecSales, fetchExecutivesAttendance } from '../../actions/authActions'

import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine.css'

const mapStateToProps = state => ({
  stations: state.auth.stations,
  attendance: state.auth.executivesAttendance,
})

class Executives extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  onShowAttendance (executiveId) {
    this.props.history.push(this.props.match.url + '/sales/view/' + executiveId)
    // this.setState({ executiveId, showAttendance: true })
  }

  onSalesEntry (executiveId) {
    this.props.history.push(this.props.match.url + '/sales/enter/' + executiveId)
    // this.setState({ executiveId, enterSales: true })
  }

  render () {
    let { executives } = this.props

    executives = executives || []
    let display = executives.map((e, i) => {
      return <Executive key={i} executive={e} onShowAttendance={this.onShowAttendance.bind(this)} onSalesEntry={this.onSalesEntry.bind(this)} />
    })

    return (
      <div style={{ borderRight: '1px solid black' }}>
        <h5> Executives:  </h5>
        {display.map((m) => m)}
      </div>
    )
  }
}

class Executive extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  render () {
    let { executive } = this.props
    let { name, _id } = executive
    return (

      <div style={{ display: 'flex', justifyContent: 'space-between', paddingBottom: 10 }} >
        <Col> {name} </Col>
        <Col> <button style={{ paddingLeft: 5, fontSize: 12 }} onClick={() => this.props.onSalesEntry(_id)}> Enter Sales </button> </Col>
        <Col> <button style={{ paddingLeft: 5, fontSize: 12 }} onClick={() => this.props.onShowAttendance(_id)}> View Sales </button> </Col>
      </div>
    )
  }
}

export default connect(
  mapStateToProps,
  { fetchGunsPerStation, fetchExecutivesPerStation, setExecSales, fetchExecutivesAttendance }
)(withRouter(Executives))
