import {
  SET_CURRENT_USER,
  FETCH_OWNER_ALL_DETAILS,
  FETCH_ALL_STATIONS,
  FETCH_STATION_DETAILS,
  FETCH_STATION_DAILY_RATES,
  FETCH_RATES_VALIDITY,
  FETCH_ALL_OWNERS,
  FETCH_GUNS_PER_STATION,
  FETCH_GUN_TYPES,
  FETCH_MANAGERS_PER_STATION,
  FETCH_EXECS_PER_STATION,
  FETCH_EXECS_ATTENDANCE,
  SET_OWNER_STATUS,
  SET_OWNER_PASSWORD,
  SET_STATION_RATES,
  SET_MANAGER_STATUS,
  SET_MANAGER_PASSWORD,
  FETCH_STOCKS_LATEST,
  FETCH_STOCKS_ALL,
  USER_LOADING
} from '../actions/types'
const isEmpty = require('is-empty')
const initialState = {
  isAuthenticated: false,
  user: {},
  owner: {},
  ownerPwd: {},
  ownerStatus: {},
  stations: [],
  stationDetails: {},
  owners: [],
  gunsPerStation: [],
  gunTypes: [],
  managersPerStation: [],
  executivesPerStation: [],
  executivesAttendance: [],
  managerStatus: {},
  stocksLatest: {},
  stocksAll: [],
  loading: false
}
export default function (state = initialState, action) {
  switch (action.type) {
    case SET_CURRENT_USER:
      return {
        ...state,
        isAuthenticated: !isEmpty(action.payload),
        user: action.payload
      }
    case SET_OWNER_STATUS:
      return {
        ...state,
        ownerStatus: action.payload
      }
    case SET_OWNER_PASSWORD:
      return {
        ...state,
        ownerPwd: action.payload
      }
    case SET_STATION_RATES:
      return {
        ...state,
        setStationRateResp: action.payload
      }
    case SET_MANAGER_STATUS:
      return {
        ...state,
        managerStatus: action.payload
      }
    case SET_MANAGER_PASSWORD:
      return {
        ...state,
        managerPassword: action.payload
      }
    case FETCH_STATION_DETAILS:
      return {
        ...state,
        stationDetails: action.payload
      }
    case FETCH_STATION_DAILY_RATES:
      return {
        ...state,
        dailyRates: action.payload
      }
    case FETCH_RATES_VALIDITY:
      return {
        ...state,
        ratesValidity: action.payload
      }
    case FETCH_ALL_STATIONS:
      return {
        ...state,
        stations: action.payload
      }
    case FETCH_ALL_OWNERS:
      return {
        ...state,
        owners: action.payload
      }
    case FETCH_GUNS_PER_STATION:
      return {
        ...state,
        gunsPerStation: action.payload
      }
    case FETCH_GUN_TYPES:
      return {
        ...state,
        gunTypes: action.payload
      }
    case FETCH_MANAGERS_PER_STATION:
      return {
        ...state,
        managersPerStation: action.payload
      }
    case FETCH_EXECS_PER_STATION:
      return {
        ...state,
        executivesPerStation: action.payload
      }
    case FETCH_EXECS_ATTENDANCE:
      return {
        ...state,
        executivesAttendance: action.payload
      }
    case FETCH_OWNER_ALL_DETAILS:
      return {
        ...state,
        owner: action.payload
      }
    case FETCH_STOCKS_LATEST:
      return {
        ...state,
        stocksLatest: action.payload
      }
    case FETCH_STOCKS_ALL:
      return {
        ...state,
        stocksAll: action.payload
      }
    case USER_LOADING:
      return {
        ...state,
        loading: true
      }
    default:
      return state
  }
}
