import React, { Component } from 'react'
import { Route, Redirect, withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import Header from './Header'
import AdminDashboard from './AdminDashboard'
import OwnerDashboard from './OwnerDashboard'
import ManagerDashboard from '../managers/ManagerDashboard'
// import T1 from './T1'

import { logoutUser } from '../../actions/authActions'

class Dashboard extends Component {
  constructor (props) {
    super(props)
    this.onLogoutClick = this.onLogoutClick.bind(this)
  }
  onLogoutClick (e) {
    e.preventDefault()
    this.props.logoutUser()
  }
  render () {
    const { auth } = this.props
    const { path } = this.props.match

    if (auth.isAuthenticated !== true) return <Route> <Redirect to='/' /> </Route>

    return (
      <div style={{ width: '100%' }}>
        <Header {...this.props} />
        <Route path={`${path}/owner/:oId`}> <OwnerDashboard {...this.props} /> </Route>
        <Route path={`${path}/manager/:mId`}> <ManagerDashboard {...this.props} /> </Route>
        <Route path={`${path}/admin`}>   <AdminDashboard {...this.props} /> </Route>
      </div>
    )

    /* <Route path={`${path}/:t1`}> <T1 /> </Route> */
  }
}
Dashboard.propTypes = {
  logoutUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
}
const mapStateToProps = state => ({
  auth: state.auth
})
export default connect(
  mapStateToProps,
  { logoutUser }
)(withRouter(Dashboard))
