import { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import { ResponsiveLine } from '@nivo/line'

import { fetchStocksLatest, fetchStocksAll } from '../../actions/authActions'

const mapStateToProps = state => ({
  stocksAll: state.auth.stocksAll
})

class ViewStock extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    this.props.fetchStocksAll({ stationId: this.props.station._id })
  }

  componentDidUpdate (prevProps, prevState) {
    if (!prevProps.show && this.props.show) {
      this.props.fetchStocksAll({ stationId: this.props.station._id })
    }
  }

  render () {
    const { station, show, stocksAll } = this.props

    let dataByType = {}
    stocksAll.forEach((entry) => {
      let date = entry.date
      for (const key in entry.stocks) {
        dataByType[key] = dataByType[key] || []
        let newDate = new Date(date)
        // let dtStr = newDate.toDateString().substr(4, 6)
        // let tmStr = newDate.toTimeString().substr(1, 4)
        // dataByType[key].push({ x: tmStr, y: entry.stocks[key] })
        dataByType[key].push({ x: newDate.toTimeString(), y: entry.stocks[key]['quantity'] })
      }
    })

    let data = []
    let keys = Object.keys(dataByType)
    keys.forEach((key) => {
      let entry = {
        id: key,
        color: 'hsl(188, 70%, 50%)',
        data: dataByType[key]
      }
      data.push(entry)
    })

    return (
      <Modal.Dialog
        show={show}
        size='lg'
        onHide={() => this.props.onCancel()}
        style={{ height: '100%', width: '100%', background: 'transparent' }}
      >
        <Modal.Header>
          <Modal.Title>Stocks for {station.name}</Modal.Title>
        </Modal.Header>

        <Modal.Body style={{ height: 600, minWidth: 600, overflow: 'scroll' }}>
          <ResponsiveLine
            data={data}
            margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
            xScale={{ type: 'point' }}
            yScale={{ type: 'linear', min: 'auto', max: 'auto' }}
            yFormat=' >-.2f'
            axisTop={null}
            axisRight={null}
            axisBottom={{
              orient: 'bottom',
              tickSize: 5,
              tickPadding: 5,
              tickRotation: 0,
              legend: 'Day of Entry',
              legendOffset: 36,
              legendPosition: 'middle'
            }}
            axisLeft={{
              orient: 'left',
              tickSize: 5,
              tickPadding: 5,
              tickRotation: 0,
              legend: 'Kilo liters',
              legendOffset: -40,
              legendPosition: 'middle'
            }}
            pointSize={10}
            pointColor={{ theme: 'background' }}
            pointBorderWidth={2}
            pointBorderColor={{ from: 'serieColor' }}
            pointLabelYOffset={-12}
            useMesh
            legends={[
              {
                anchor: 'bottom-right',
                direction: 'column',
                justify: false,
                translateX: 100,
                translateY: 0,
                itemsSpacing: 0,
                itemDirection: 'left-to-right',
                itemWidth: 80,
                itemHeight: 20,
                itemOpacity: 0.75,
                symbolSize: 12,
                symbolShape: 'circle',
                symbolBorderColor: 'rgba(0, 0, 0, .5)',
                effects: [
                  {
                    on: 'hover',
                    style: {
                      itemBackground: 'rgba(0, 0, 0, .03)',
                      itemOpacity: 1
                    }
                  }
                ]
              }
            ]}
          />
        </Modal.Body>

        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.onCancel()}> Close </Button>
        </Modal.Footer>
      </Modal.Dialog>
    )
  }
}

export default connect(
  mapStateToProps,
  { fetchStocksLatest, fetchStocksAll }
)(withRouter(ViewStock))
