import { Component } from 'react'
import Modal from 'react-bootstrap/Modal'
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Button from 'react-bootstrap/Button'

class AddStock extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  addStockQuantity (gunType, e) {
    const { value } = e.target
    let newStocks = { ...this.state.stocks }
    newStocks[gunType] = newStocks[gunType] || { quantity: '', value: '' }
    newStocks[gunType]['quantity'] = value

    this.setState({ stocks: newStocks })
  }

  addStockValue (gunType, e) {
    const { value } = e.target
    let newStocks = { ...this.state.stocks }
    newStocks[gunType] = newStocks[gunType] || { quantity: '', value: '' }
    newStocks[gunType]['value'] = value

    this.setState({ stocks: newStocks })
  }

  render () {
    const { station, guns, show } = this.props
    const allTypes = guns.map((g) => g.type)
    const unique = allTypes.filter((t, i, ar) => ar.indexOf(t) === i)

    const display = unique.map((gunType, i) => {
      return <div key={i} style={{ display: 'flex' }}>
        <Row style={{ marginTop: 5 }}>
          <Col> {gunType} : </Col>
          <Col> <Form.Control type='number' placeholder='Stock (Quantity)' onChange={this.addStockQuantity.bind(this, gunType)} /></Col>
          <Col> <Form.Control type='number' placeholder='Value (Rupees)' onChange={this.addStockValue.bind(this, gunType)} /></Col>
        </Row>
      </div>
    })

    return (
      <Modal.Dialog
        show={show}
        size='lg'
        backdrop='static'
        onHide={() => this.props.onCancel()}
        style={{ height: '100%', width: '100%', background: 'transparent' }}
      >
        <Modal.Header>
          <Modal.Title>Add Stock for {station.name}</Modal.Title>
        </Modal.Header>

        <Modal.Body style={{ height: 'inherit', minHeight: 'inherit' }}>
          <Row>
            <Col> <Form.Control type='text' placeholder='Invoice Number' onChange={(e) => this.setState({ invoice: e.target.value })} /> </Col>
          </Row>
          { display.map((d) => d) }
        </Modal.Body>

        <Modal.Footer>
          <Button variant='secondary' onClick={() => this.props.onCancel()}> Cancel </Button>
          <Button variant='primary' onClick={() => this.props.onSubmit(this.state)}> Submit </Button>
        </Modal.Footer>
      </Modal.Dialog>
    )
  }
}

export default AddStock
