import { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// import Row from 'react-bootstrap/Row'
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import { setManagerStatus, setManagerPassword } from '../../actions/authActions'

import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine.css'

const mapStateToProps = state => ({
  stations: state.auth.stations
})

class Managers extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    // let { station } = this.props
  }

  componentDidUpdate (prevProps, prevState) {
    let prevStation = prevProps.station
    let { station } = this.props
    if (prevStation._id !== station._id) {
    }
  }

  onToggleManagerActive (managerId, value) {
    let { _id } = this.props.station
    this.props.setManagerStatus({ managerId, value, stationId: _id })
    // this.setState({ managerId, showGunStats: true })
  }

  onShowSetPassword (managerId) {
    this.setState({ selectedManagerId: managerId, showSetPassword: true })
  }

  onSetPassword (newPassword) {
    let { selectedManagerId } = this.state
    let { _id } = this.props.station
    this.props.setManagerPassword({ managerId: selectedManagerId, newPassword, stationId: _id })
    this.setState({ selectedManagerId: null, showSetPassword: false })
  }

  render () {
    let { selectedManagerId, showSetPassword } = this.state
    let { managers } = this.props
    managers = managers || []
    let managersDisplay = managers.map((m, i) => {
      return <Manager key={i} manager={m} onToggleManagerActive={this.onToggleManagerActive.bind(this)} onShowSetPassword={this.onShowSetPassword.bind(this)} />
    })

    return (
      <div style={{ borderRight: '1px solid black' }}>
        <h5> Managers:  </h5>
        {managersDisplay.map((m) => m)}
        { showSetPassword ? <SetPassword selectedManagerId={selectedManagerId} onSubmit={this.onSetPassword.bind(this)} onCancel={() => this.setState({ selectedManagerId: null, showSetPassword: false })} /> : null }
      </div>
    )
  }
}

class SetPassword extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  render () {
    let { newPassword } = this.state
    let { onCancel, onSubmit } = this.props
    return (
      <Modal
        show
        size='md'
        backdrop='static'
        onHide={() => this.props.onCancel()}
        style={{ height: '100%', width: '100%', background: 'transparent' }}
      >
        <Modal.Header>
          <Modal.Title>Set Password</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ height: 'inherit', minHeight: 'inherit' }}>
          <Form.Group controlId='formBasicPassword'>
            <Form.Label>Password</Form.Label>
            <Form.Control type='password' value={newPassword} onChange={(e) => this.setState({ newPassword: e.target.value })} placeholder='Password' />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant='secondary' onClick={() => onCancel()}> Cancel </Button>
          <Button variant='primary' onClick={() => onSubmit(newPassword)}> Submit </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}

class Manager extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
  }

  componentDidUpdate (prevProps, prevState) {
  }

  render () {
    let { manager } = this.props
    let { username, isActive } = manager
    let color = isActive ? 'red' : 'green'
    return (
      <div style={{ display: 'flex', justifyContent: 'space-between', paddingBottom: 10 }} >
        <Col> {username} </Col>
        <Col> <button style={{ paddingLeft: 5, fontSize: 12, background: color, color: 'white' }} onClick={() => this.props.onToggleManagerActive(manager._id, !isActive)}> {isActive ? 'Disable' : 'Enable'} </button> </Col>
        <Col> <button style={{ paddingLeft: 5, fontSize: 12 }} onClick={() => this.props.onShowSetPassword(manager._id)}> Reset Password </button> </Col>
      </div>
    )
  }
}

