import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
// import Row from 'react-bootstrap/Row'

import DisplayStationDetails from '../dashboard/DisplayStationDetails'
import { fetchExecutivesPerStation, fetchStationDetails } from '../../actions/authActions'

// import Executives from '../executives/Executives'

const mapStateToProps = state => ({
  user: state.auth.user,
  stationDetails: state.auth.stationDetails,
  executives: state.auth.executivesPerStation
})

class ManagerDashboard extends Component {
  constructor (props) {
    super(props)
  }

  componentDidMount () {
    const { user } = this.props
    this.props.fetchStationDetails({ stationId: user.stationId })
    // this.props.fetchExecutivesPerStation({ stationId: user.stationId })
  }

  // <Row><Executives executives={this.props.executives} /> </Row>
  render () {
    const station = this.props.stationDetails
    return <div>
    { station ? <DisplayStationDetails disableManagerDisplay station={station} /> : null }
    </div>
  }
}

ManagerDashboard.propTypes = {
  user: PropTypes.object.isRequired
}
export default connect(
  mapStateToProps,
  { fetchExecutivesPerStation, fetchStationDetails }
)(ManagerDashboard)
