import React, { Component } from 'react'
// import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'
import Row from 'react-bootstrap/Row'
// import Col from 'react-bootstrap/Col'
// import Form from 'react-bootstrap/Form'
import { connect } from 'react-redux'
import { loginUser } from '../../actions/authActions'
import classnames from 'classnames'

class Login extends Component {
  constructor () {
    super()
    this.state = {
      username: '',
      password: '',
      type: 'OWNER',
      errors: {}
    }
    this.onChange = this.onChange.bind(this)
    this.onSubmit = this.onSubmit.bind(this)
  }
  componentDidMount () {
    // If logged in and user navigates to Login page, should redirect them to dashboard
    if (this.props.auth.isAuthenticated) {
      const { user } = this.props.auth
      if (user.username === 'admin') {
        this.props.history.push('/dashboard/admin')
      } else {
        const role = user.role ? user.role.toLowerCase() : 'unknown'
        this.props.history.push('/dashboard/' + role + '/' + user.id)
      }
    }
  }

  componentDidUpdate (prevProps, prevState) {
    if (this.props.auth.isAuthenticated) {
      const { user } = this.props.auth
      if (user.username === 'admin') {
        this.props.history.push('/dashboard/admin')
      } else {
        const role = user.role ? user.role.toLowerCase() : 'unknown'
        this.props.history.push('/dashboard/' + role + '/' + user.id)
      }
    }
  }
  /*
    if (this.props.errors) {
      this.setState({
        errors: this.props.errors
      })
    }
    */

  onChange (e) {
    this.setState({ [e.target.id]: e.target.value })
  }
  onSubmit (e) {
    e.preventDefault()
    const userData = {
      username: this.state.username,
      password: this.state.password,
      type: this.state.type
    }
    this.props.loginUser(userData)
  }
  render () {
    const { errors } = this.state

    return (
      <div style={{ margin: 'auto', display: 'flex' }}>
        <Row style={{ margin: 'auto', display: 'block' }}>
          <Row> <b>Login</b> </Row>
          <form noValidate onSubmit={this.onSubmit}>
            <Row style={{ marginLeft: 20 }}>
              <label htmlFor='OWNER' style={{ paddingLeft: 15 }}>Owner</label>
              <input
                onClick={() => this.setState({ type: 'OWNER' })}
                error={errors.type}
                type='radio'
                id='type'
                name='type'
                checked={this.state.type === 'OWNER'}
              />
              <label htmlFor='MANAGER' style={{ paddingLeft: 15 }}>Manager</label>
              <input
                onClick={() => this.setState({ type: 'MANAGER' })}
                error={errors.type}
                type='radio'
                id='type'
                name='type'
                checked={this.state.type === 'MANAGER'}
              />
            </Row>

            <Row>
              <label htmlFor='username'>username</label>
              <input
                onChange={this.onChange}
                value={this.state.username}
                error={errors.username}
                id='username'
                type='username'
                className={classnames('', {
                  invalid: errors.username || errors.usernameNotFound
                })}
              />
              <span className='red-text'>
                {errors.username}
                {errors.usernameNotFound}
              </span>
            </Row>
            <Row>
              <label htmlFor='password'>Password</label>
              <input
                onChange={this.onChange}
                value={this.state.password}
                error={errors.password}
                id='password'
                type='password'
                className={classnames('', {
                  invalid: errors.password || errors.passwordincorrect
                })}
              />
              <span className='red-text'>
                {errors.password}
                {errors.passwordincorrect}
              </span>
            </Row>
            <Row style={{ paddingLeft: '11.250px' }}>
              <button
                style={{
                  border: '1px solid black',
                  width: '150px',
                  borderRadius: '3px',
                  letterSpacing: '1.5px',
                  marginTop: '1rem'
                }}
                type='submit'
                className='btn btn-large waves-effect waves-light hoverable blue accent-3'
              >
                Login
              </button>
            </Row>
          </form>
        </Row>
      </div>
    )
  }
}

Login.propTypes = {
  loginUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
}
const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
})
export default connect(
  mapStateToProps,
  { loginUser }
)(Login)
