import { Component } from 'react'

import Button from 'react-bootstrap/Button'

import { AgGridColumn, AgGridReact } from 'ag-grid-react'

class ViewSales extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  componentDidMount () {
    let { executiveId } = this.props
    this.props.fetchExecutivesAttendance({ executiveId })
  }

  componentDidUpdate (prevProps, prevState) {
  }

  render () {
    let { executiveId, executives, attendance } = this.props
    let exec = executives.find((e) => e._id === executiveId)
    exec = exec || {}

    let rowData = attendance.map((a) => {
      return ({
        inTime: new Date(a.inTime).toString(),
        outTime: new Date(a.outTime).toString(),
        cashSales: a.cashSales
      })
    })
    return (
      <div>
        <h3> {exec.name} </h3>
        <div className='ag-theme-alpine-dark' style={{ height: 500, width: window.innerWidth }}>
          <AgGridReact rowData={rowData} rowSelection='single'>
            <AgGridColumn field='inTime' sortable filter />
            <AgGridColumn field='outTime' sortable filter />
            <AgGridColumn field='cashSales' sortable filter />
          </AgGridReact>
          <Button variant='secondary' onClick={() => this.props.hide()}> Close </Button>
        </div>
      </div>
    )
  }
}

export default ViewSales
