export const registerNewOwner = (userData) => dispatch => {
  axios
    .post('/owner/add', userData)
    // .then(res => history.push('/login')) // re-direct to login on successful register
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    )
}

